import React, { FC } from 'react';
import {
  ColumnsProps,
  DefaultSortProps,
  FilterType,
  getExtraData,
  OrderType,
  percentualFormatter,
  SearchDataGrid,
  SearchViewProps,
  WindowComponent,
} from 'summer';
import { AtividadeEconomica, CLASS_NAME } from '../../../../models/dtos/mlf/atividadeEconomica';

const defaultSorted: DefaultSortProps[] = [
  {
    dataField: 'codigo',
    order: OrderType.ASC,
  },
];

const AtividadeEconomicaSearchView: FC<SearchViewProps<AtividadeEconomica>> = props => {
  const columns: ColumnsProps[] = [
    {
      dataField: 'codigo',
      editable: false,
      filter: {
        type: FilterType.TEXT,
      },
      sort: true,
      text: 'Código',
    },
    {
      dataField: 'descricao',
      editable: true,
      filter: {
        type: FilterType.TEXT,
      },
      sort: true,
      text: 'Descrição',
    },
    {
      dataField: 'aliquota',
      editable: true,
      filter: {
        type: FilterType.NUMBER,
      },
      formatter: percentualFormatter,
      sort: true,
      text: 'Alíquota',
    },
    {
      dataField: 'dtInicioEscrituracao',
      editable: true,
      filter: {
        type: FilterType.DATE,
      },
      sort: true,
      text: 'Data Início',
    },
    {
      dataField: 'dtTerminoEscrituracao',
      editable: true,
      filter: {
        type: FilterType.DATE,
      },
      sort: true,
      text: 'Data Término',
    },
  ];
  return (
    <WindowComponent title="Atividade Econômica">
      <SearchDataGrid
        {...props}
        {...props.searchDataGridProps}
        columns={columns}
        defaultSorted={defaultSorted}
        dtoClassName={CLASS_NAME}
        extraData={getExtraData(props, 'AtividadeEconomicaEditView', CLASS_NAME)}
      />
    </WindowComponent>
  );
};

export default AtividadeEconomicaSearchView;
